    <contentLoading text="Loading, please wait..." v-if="isLoading == true" color="#fc9b21" />
    <previewImage :imageURL="previewImg" v-if="previewImg" @close-preview="PREVIEW_PIC_CLOSE()" />
  </div>
</template> 

<script>
//API
import axios from "/axios.js";
import moment from "moment";

//Components
import VueTabsChrome from "vue-tabs-chrome";
import contentLoading from "@/components/app-structures/app-content-loading.vue";
import previewImage from "@/components/image-preview.vue";

//DataGrid

export default {
  name: "ViewTankInformation",
  components: {
    VueTabsChrome,
    contentLoading,
    previewImage
  },
  created() {
    this.$store.commit("UPDATE_CURRENT_PAGENAME", {
      subpageName: "Pipeline Information",
      subpageInnerName: null
    });
    this.FETCH_INFO();
  },
  data() {
    return {
      infoFlowline: {},
      file_pic_upload: "",
      tabCurrent: "info",
      tabs: [
        {
          label: "General Information",
          key: "info",
          closable: false
        }
        // {
        //   label: "Additional Information",
        //   key: "additional",
        //   closable: false
        // },
      ],
      previewImg: "",
      isLoading: false
    };
  },
  computed: {
    generalInfo() {
      var info = [
        {
          desc: "Piping Code",
          value: this.infoFlowline.piping_code
        },
        {
          desc: "Pipe Spec",
          value: this.infoFlowline.pipe_spec
        },
        {
          desc: "PID No.",
          value: this.infoFlowline.pid_no
        },
        {
          desc: "Service",
          value: this.infoFlowline.id_service
        },
        {
          desc: "Material",
          value: this.infoFlowline.id_material
        },
        {
          desc: "NPS",
          value: this.infoFlowline.nps
        },
        {
          desc: "Stress",
          value: this.infoFlowline.stress
        },
        {
          desc: "Joint Efficiency",
          value: this.infoFlowline.E
        },
        {
          desc: "Design Pressure (psi)",
          value: this.infoFlowline.design_pressure_psi
        },
        {
          desc: "Operating Pressure (psi)",
          value: this.infoFlowline.operating_pressure_psi
        },
        {
          desc: "Design Temperature (C)",
          value: this.infoFlowline.design_temp_max_c
        },
        {
          desc: "Operating Temperature (psi)",
          value: this.infoFlowline.operating_pressure_psi
        },
        {
          desc: "Dead Leg",
          value: this.infoFlowline.deadleg
        },
        {
          desc: "Insulation Type",
          value: this.infoFlowline.insulation_type
        },
        {
          desc: "Insulation Size",
          value: this.infoFlowline.insulation_size
        },
        {
          desc: "CAD",
          value: this.infoFlowline.CAd
        },
        {
          desc: "LD",
          value: this.infoFlowline.Ld
        },
        {
          desc: "-",
          value: this.infoFlowline.last_inspection_date
            ? moment(this.infoFlowline.last_inspection_date).format("LL")
            : null
        }
      ];
      return info;
    },
    componentInfo() {
      var info = [
        {
          desc: "Bottom",
          value: this.infoFlowline.component_bottom
        },
        {
          desc: "Shell",
          value: this.infoFlowline.component_shell
        },
        {
          desc: "Manways",
          value: this.infoFlowline.component_manways
        },
        {
          desc: "Fixed Roof",
          value: this.infoFlowline.component_fixed_roof
        },
        {
          desc: "Access",
          value: this.infoFlowline.component_access
        },
        {
          desc: "Drain",
          value: this.infoFlowline.component_drain
        },
        {
          desc: "Vents",
          value: this.infoFlowline.component_vents
        },
        {
          desc: "Floating Roof",
          value: this.infoFlowline.floating_roof
        },
        {
          desc: "Type",
          value: this.infoFlowline.component_type
        },
        {
          desc: "Material and Style",
          value: this.infoFlowline.component_material_and_style
        },
        {
          desc: "Primary Seal",
          value: this.infoFlowline.component_primary_seal
        },
        {
          desc: "Secondary Seal",
          value: this.infoFlowline.component_secondary_seal
        },
        {
          desc: "Anti Rotation Device",
          value: this.infoFlowline.component_anti_rotation_device
        },
        {
          desc: "Gauge Pipe",
          value: this.infoFlowline.component_gauge_pipe
        }
      ];
      return info;
    },

    baseURL() {
      var mode = this.$store.state.mode;
      if (mode == "dev") return this.$store.state.modeURL.dev;
      else if (mode == "prod") return this.$store.state.modeURL.prod;
      else return console.log("develpment mode set up incorrect.");
    }
  },
  watch: {
    // watch for changes in the $route object
    $route: {
      handler() {
        // run this logic whenever router is change
        this.FETCH_INFO();
      }
      //immediate: true // call the handler immediately when the component is created
    }
  },
  methods: {
    FETCH_INFO() {
      console.log("==> FETCH FLOWLINE INFO: ");
      //this.isLoading = true;
      const id_tag = this.$route.params.id_tag;

      axios({
        method: "get",
        url: "FlowlineInfo/" + id_tag,
        headers: {
          Authorization: "Bearer " + JSON.parse(localStorage.getItem("token"))
        },
        data: {}
      })
        .then(res => {
          if (res.status == 200 && res.data) {
            this.infoFlowline = res.data;
            console.log(this.infoFlowline);
          }
        })
        .catch(error => {
          console.log(error);
        })
        .finally(() => {
          this.isLoading = false;
        });
    },
    UPLOAD_PIC(pic_type) {
      this.isLoading = true;
      console.log("==> TANK INFO IMG UPLOAD: START");
      var id_tag = this.$route.params.id_tag;
      console.log("==> ID TAG (TANK): " + id_tag);

      if (pic_type == 1) {
        this.file_pic_upload = this.$refs.pic_upload_ov.files[0];
      } else {
        this.file_pic_upload = this.$refs.pic_upload_np.files[0];
      }

      console.log(this.file_pic_upload);
      console.log("==> TANK INFO IMG UPLOAD: TYPE " + pic_type);
      if (
        (this.file_pic_upload && this.file_pic_upload.type == "image/jpeg") ||
        this.file_pic_upload.type == "image/png"
      ) {
        if (this.file_pic_upload.size < 20000000) {
          axios({
            method: "post",
            url: "/tank-info/attach-pic",
            headers: {
              "Content-Type": "multipart/form-data",
              Authorization:
                "Bearer " + JSON.parse(localStorage.getItem("token"))
            },
            data: {
              id_tag: id_tag,
              file: this.file_pic_upload,
              type: pic_type
            }
          })
            .then(res => {
              console.log(res);

              if (res.status == 200) {
                this.FETCH_TANK_INFO();
                console.log("==> TANK INFO IMG UPLOAD: DONE");
                this.isLoading = false;
              }
            })
            .catch(error => {
              this.isLoading = false;
              this.$ons.notification.alert(
                error.code + " " + error.response.status + " " + error.message
              );
            })
            .finally(() => {});
        } else {
          this.$ons.notification.alert("File size too large. (20 MB max)");
          const file = document.getElementById("pic-upload-btn");
          file.val = "";
          this.isLoading = false;
        }
      } else {
        this.$ons.notification.alert(
          "Incorrect filetype. <br/> Only JPEG/PNG file can be uploaded."
        );
        const file = document.getElementById("pic-upload-btn");
        file.val = "";
        this.isLoading = false;
      }
    },
    DELETE_PIC(file_path, pic_type) {
      this.$ons.notification.confirm("Confirm delete?").then(res => {
        if (res == 1) {
          this.isLoading = true;
          console.log("==> TANK INFO IMG DELETE: START");
          var id_tag = this.$route.params.id_tag;
          console.log("==> ID TAG (TANK): " + id_tag);
          axios({
            method: "delete",
            url: "/tank-info/delete-pic",
            headers: {
              Authorization:
                "Bearer " + JSON.parse(localStorage.getItem("token"))
            },
            data: {
              id_tag: id_tag,
              file_path: file_path,
              type: pic_type
            }
          })
            .then(res => {
              console.log(res);

              if (res.status == 200) {
                this.FETCH_TANK_INFO();
                console.log("==> TANK INFO IMG DELETE: DONE");
                this.isLoading = false;
              }
            })
            .catch(error => {
              this.isLoading = false;
              this.$ons.notification.alert(
                error.code + " " + error.response.status + " " + error.message
              );
            })
            .finally(() => {
              this.FETCH_TANK_INFO();
            });
        }
      });
    },
    PREVIEW_PIC(img) {
      if (img) {
        this.previewImg = img;
      }
    },
    PREVIEW_PIC_CLOSE() {
      this.previewImg = "";
    }
  }
};
</script>

